@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
/* Variables */
:root {
  --primary-light: #fafafa;
  --secondary-light: #dadada;
  --primary-dark: #1c1c1c;
  --primary-text: #1c1c1c;
}

[data-theme='dark'] {
  --primary-light: #1c1c1c;
  --secondary-light: #dadada;
  --primary-dark: #fafafa;
  --primary-text: #1c1c1c;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 64px;
  padding-bottom: 64px;
  background-color: #1c1c1c;
  background-color: var(--primary-dark);
  color: #fafafa;
  color: var(--primary-light);
}

.dark-mode {
  background-color: #fafafa;
  background-color: var(--primary-light);
  color: #1c1c1c;
  color: var(--primary-dark);
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 18px;
  border: 1px solid black;
  background-color: #dadada;
  background-color: var(--secondary-light);

  color: #1c1c1c;

  color: var(--primary-text);
}

.coin-input::-webkit-input-placeholder {
  color: #1c1c1c;
  color: var(--primary-text);
}

.coin-input:-ms-input-placeholder {
  color: #1c1c1c;
  color: var(--primary-text);
}

.coin-input::-ms-input-placeholder {
  color: #1c1c1c;
  color: var(--primary-text);
}

.coin-input::placeholder {
  color: #1c1c1c;
  color: var(--primary-text);
}

/* Dark mode switch */
.theme-switch-wrapper {
  position: absolute;
  top: 24px;
  right: 124px;
  display: flex;
  align-items: center;
}

em {
  margin-left: 10px;
  font-size: 1rem;
}

.theme-switch {
  margin-left: 8px;
  display: inline-block;
  height: 34px;
  position: relative;
  width: 60px;
}

.theme-switch input {
  display: none;
}

.slider {
  background-color: #ccc;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.4s;
}

.slider:before {
  background-color: #fff;
  bottom: 4px;
  content: '';
  height: 26px;
  left: 4px;
  position: absolute;
  transition: 0.4s;
  width: 26px;
}

input:checked + .slider {
  background-color: #66bb6a;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.coin-container {
  display: flex;
  justify-content: center;
}

.coin-row {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  width: 900px;
}

.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;

  min-width: 300px;
}

.coin h1 {
  font-size: 16px;
  width: 150px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin-symbol {
  text-transform: uppercase;
}

.coin-data {
  display: flex;
  text-align: right;
  justify-content: space-between;
  width: 100%;
}

.coin-price {
  width: 110px;
}

.coin-volume {
  width: 155px;
}

.coin-marketcap {
  width: 230px;
}

.coin-percent {
  width: 100px;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}

